.employees__header-text{color:#8a2be2;font-family:"Exo 2",sans-serif;font-style:normal;font-size:40px}.subtitle{font-family:"Exo 2",sans-serif;font-style:normal;font-weight:600;font-size:28px;margin-left:20px}.form__panel{display:flex;flex-direction:column;justify-content:flex-start;align-items:flex-start;border:1px solid #000;border-radius:4px;margin-top:15px;background-color:wheat}.panel{border-radius:4px;font-weight:600}.field__group{display:flex;flex-direction:column;margin:4px;padding:6px;margin-left:16px}.label{font-family:"Exo 2",sans-serif;font-style:normal;font-weight:600;display:flex;align-items:center;width:70px;margin-right:10px}.field{display:flex;flex-direction:column;align-items:flex-start}.field__gender{display:block}.form__submit-btn{display:flex;flex-direction:column;align-items:center}input{background-color:#f0f8ff;font-style:italic;font-weight:600;padding-left:10px}span::after{padding-left:5px}.date-input{margin-left:10px;font-style:normal}.date-input:invalid+span::after{content:"✖"}.date-input:valid+span::after{content:"✓"}select{margin-top:10px;margin-bottom:10px;font-style:italic;font-weight:600}.date__text{font-weight:600;margin-top:10px;margin-bottom:10px}.button{margin-bottom:10px;background-color:#fff;color:#000;border:1px solid #000;border-radius:4px;cursor:pointer}.button:hover{background-color:#faebd7;color:#000;font-weight:700}